.switcher {
  align-items: center;
  margin-left: auto;
  margin-right: 14px;
  display: flex;
}

.switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switch input {
  display: none;
}

.slider {
  cursor: pointer;
  background: linear-gradient(#4f2ee8, #dcdcdc);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 18px;
  width: 18px;
  background-color: #fff;
  transition: all .4s;
  position: absolute;
  top: 1px;
  left: 1px;
}

input:checked + .slider {
  background: linear-gradient(#4f2ee8, #686868);
}

input:focus + .slider {
  box-shadow: 0 0 1px #4f2ee8;
}

input:checked + .slider:before {
  transform: translateX(20px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.menu-open {
  cursor: pointer;
  background-color: #fff;
  border: 0;
}

.user-btn {
  display: none;
}

.icon-logo {
  height: 24px;
  width: 24px;
}

.icon-shopping {
  width: 20px;
  height: 20px;
}

.icon-switcher {
  width: 40px;
  height: 24px;
}

.icon-user-support {
  height: 26px;
  width: 23px;
}

.icon-menu-open {
  width: 28px;
  height: 28px;
}

